.intro {
    width: 100vw;
    min-height: calc(100vh - 170px);
    max-height: fit-content;
    background-color: white;
    //background-image: url("https://images.unsplash.com/photo-1542831371-29b0f74f9713?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MXx8cHJvZ3JhbW1pbmd8ZW58MHx8MHx8&w=1000&q=80");
    border-bottom: 1px solid lightgray;

    @media screen and (min-width:992px) {
        background-image: linear-gradient(0deg, rgba(255, 255, 255, 0.65), rgba(255, 255, 255, 0.65)), url("../../assets/webp/intro-background1.webp");
        background-size: cover;
        background-repeat: no-repeat;
        height: calc(100vh - 170px);

    }

    @media screen and (max-width: 992px) {
        min-height: calc(100vh - 100px);
        max-height: none;
        height: none;
    }

    .intro-container {
        min-height: 100%;
        height: 100%;

        @media screen and (max-width: 992px) {
            border-bottom: 0px solid lightgrey;
        }

        .intro-row {
            min-height: 100%;

            @media screen and (max-width: 992px) {
                min-height: calc(85%);
            }

            .intro-left {
                min-height: inherit;
                display: flex;
                min-height: 100%;
                padding: 30px;
                padding-top: 0px;
                flex-direction: column;
                align-items: flex-start;
                justify-content: center;

                @media (min-width: 768px) and (max-width: 992px) {
                    padding-top: 60px;
                }

                @media screen and (max-width: 768px) {
                    padding-top: 60px;

                }

                h1 {
                    text-align: left;
                    font-weight: bold;
                    font-family: inherit;
                    font-size: 38px;
                    min-height: 75px;
                    letter-spacing: -0.2px;

                    @media screen and (max-width: 768px) {
                        font-size: 31px;
                    }

                }

                p {
                    font-size: 25px;

                    @media screen and (max-width: 768px) {
                        font-size: 20px;
                    }
                }
            }

            .intro-right {
                //background-color: green;
                min-height: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                height: 100%;
                padding: 0px !important;

                @media screen and (max-width: 992px) {}

                .photo-container {
                    width: 100%;
                    height: 100%;

                    .photo {
                        height: calc(100vh - 170px);
                        object-fit: cover !important;
                        position: relative;

                        @media screen and (min-width: 992px) {
                            margin: 0px 10px;
                        }

                        @media screen and (max-width: 992px) {
                            height: auto;
                            margin: 0px 5px;
                            width: calc(100% - 10px);
                        }
                    }
                }

                .photo-cover-responsive {
                    height: 100px;
                    width: 100%;
                    //background-color: white !important;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                }
            }
        }


    }
}